import React, { Component } from 'react';
import Header from './Header';
import Skills from './Skills';
import Education from './Education';
import Projects from './Projects';
import scrollToComponent from 'react-scroll-to-component';
import classNames from 'classnames';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';

class Main extends Component{

    state = {
        active : 'main'
    }

    componentDidMount(){
       
        setInterval(() => {
         if(window.scrollY < '500'){
            this.setState({
                active: 'main'
            })
         }else if(window.scrollY > '500' && window.scrollY < '1000'){
            this.setState({
                active: 'skills'
            })
         }else if(window.scrollY > '1200' && window.scrollY < '1800' ){
            this.setState({
                active: 'education'
            })
         }else if(window.scrollY > '1800'){
            this.setState({
                active: 'project'
            }) 
         }
        }, 1000);
    }

    focusFunc = (e) => {
        console.log(e.target.parentElement.parentElement.classList)
        if(e.target.parentElement.parentElement.classList[1] == 'main'){
            scrollToComponent(this.Header)
            this.setState({
                active: 'main'
            })
        }else if(e.target.parentElement.parentElement.classList[1] == 'education'){
            scrollToComponent(this.Education)
            this.setState({
                active: 'education'
            })
        }else if(e.target.parentElement.parentElement.classList[1] == 'project'){
            scrollToComponent(this.Projects)
            this.setState({
                active: 'project'
            })
        }else{
            scrollToComponent(this.Skills)
            this.setState({
                active: 'skills'
            })
        }
    }


    render(){
        return(
            <div>
                <div className='jumbotron' style={{zIndex:2}}>
                    <nav className="navbar navbar-dark navbar-expand-lg" style={{backgroundColor:"#350438", color:"#eb34e1"}}>
                        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span class="navbar-toggler-icon"></span>
                        </button>
                        <div class="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav">
                                <li className={classNames("nav-item main", this.state.active == 'main' ? 'active' : '')} onClick={this.focusFunc}><a className="nav-link" href="#"><strong>Main</strong></a></li>
                                <li className={classNames("nav-item skills", this.state.active == 'skills' ? 'active' : '')} onClick={this.focusFunc}><a className="nav-link" href="#"><strong>Skills</strong></a></li>
                                <li className={classNames("nav-item education", this.state.active == 'education' ? 'active' : '')} onClick={this.focusFunc}><a className="nav-link" href="#"><strong>Education</strong></a></li>
                                <li className={classNames("nav-item project", this.state.active == 'project' ? 'active' : '')} onClick={this.focusFunc}><a className="nav-link" href="#"><strong>Projects</strong></a></li>
                            </ul>
                        </div>
                    </nav>
                </div>
                <Header ref = {(element) => this.Header = element} focusEle={this.focusFunc}/>
                <div className="container">
                <Skills ref = {(element) => this.Skills = element}/>
                <Education ref = {(element) => this.Education = element}/>
                <Projects ref = {(element) => this.Projects = element} />
                </div>
                </div>
            
        )
    }
}

export default Main;